$window-container-width: 1920px;

$main-color: #FFFFFF;
$select-color: #FF005A;

@function transformToVW($size) {
  @return calc($size / $window-container-width * 100vw);
}


.font-pop {
  position: fixed;

  top: 0;
  left: 0;

  width: 100vw;
  height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;

  &__back {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color: #313438, $alpha: 0.4);

    z-index: 1;
  }

  &__content {
    width: transformToVW(1150px);
    height: 70%;

    padding: transformToVW(30px);

    background-color: #313438;

    display: grid;
    grid-template-columns: transformToVW(385px) 1fr transformToVW(20px);
    grid-template-rows: transformToVW(20px) 1fr;
    gap: transformToVW(30px);

    z-index: 2;

    &-create {
      grid-column: 1/2;
      grid-row: 1/3;

      &-header {
        margin-bottom: transformToVW(30px);
      }

      &-text-item {
        display: flex;
        justify-content: space-between;
        align-items: center;

        margin-bottom: transformToVW(15px);

        input {
          height: transformToVW(30px);
          width: transformToVW(190px);

          padding: 0 transformToVW(15px);

          background: #2D2F33;
          border: none;
          border-radius: 0;
          outline: none;

          border-bottom: transformToVW(1px) solid #575C66;

          font: inherit;
          color: inherit;
        }
      }

      &-upload-item {
        position: relative;
        margin: transformToVW(30px) 0;
        width: transformToVW(310px);

        input {
          position: absolute;
          top: 0;
          left: 0;
          opacity: 0;
          pointer-events: none;
        }

        &-text-label {
          display: block;
          margin-bottom: transformToVW(25px);
        }

        &-zone-label {
          display: block;
          width: 100%;
          height: transformToVW(130px);
          background: #2D2F33;
          border-bottom: transformToVW(1px) solid #575C66;

          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;

          text-align: center;

          img {
            height: transformToVW(22.27px);
            margin-bottom: transformToVW(6.23px);
          }
        }
      }

      &-btn {
        width: transformToVW(160px);
        height: transformToVW(40px);

        background: $select-color;
        outline: none;
        border: none;

        cursor: pointer;

        font: inherit;
        color: inherit;
      }
    }

    &-connect {
      grid-column: 2/3;
      grid-row: 1/3;

      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: max-content 1fr;
      gap: transformToVW(30px);

      &-list {
        &-item {
          display: flex;
          justify-content: flex-start;
          align-items: center;

          margin-bottom: transformToVW(15px);

          cursor: pointer;

          &-square {
            width: transformToVW(25px);
            height: transformToVW(25px);
            margin-right: transformToVW(20px);
            background-color: #2D2F33;

            transition: background-color ease-in-out 400ms;
          }

          &:hover &-square {
            background-color: rgba($color: $select-color, $alpha: 0.4);
          }
        }

        &-item_active &-item-square,
        &-item_active:hover &-item-square {
          background-color: $select-color;
        }
      }
    }

    &-exit {
      grid-column: 3/4;
      grid-row: 1/2;

      background: none;
      border: none;
      outline: none;

      cursor: pointer;

      img {
        width: 100%;
        height: 100%;
      }
    }
  }
}