$window-container-width: 1920px;

$main-color: #FFFFFF;
$select-color: #FF005A;

@function transformToVW($size) {
  @return calc($size / $window-container-width * 100vw);
}

.tag-settings {
  position: relative;
  padding: transformToVW(15px);

  &__open-btn {
    display: flex;
    height: transformToVW(40px);
    width: 100%;

    align-items: center;
    justify-content: space-between;

    padding: 0 transformToVW(10px);

    border: transformToVW(1px) solid #575C66;
    background: #2D2F33;
    outline: none;

    cursor: pointer;

    font: inherit;
    color: inherit;

    outline: none;
    border-radius: 0;

    text-align: left;
  }

  &__drop-menu {
    position: absolute;
    top: 95%;
    left: transformToVW(15px);

    width: transformToVW(240px);

    opacity: 0;
    pointer-events: none;

    background: #2D2F33;
    
    transition: opacity ease-in-out 400ms;

    &-item {
      display: block;
      height: transformToVW(40px);
      width: 100%;

      padding: 0 transformToVW(10px);

      border: transformToVW(1px) solid #575C66;
      background: none;
      outline: none;

      background-color: #2D2F33;

      cursor: pointer;

      font: inherit;
      color: inherit;

      outline: none;
      border-radius: 0;

      text-align: left;

      transition: background-color ease-in-out 400ms; 

      &:hover {
        background-color: rgba($select-color, 0.4);
      }
    }

    &-item_active {
      background-color: $select-color;

      &:hover {
        background-color: $select-color;
      }
    }
  }

  &__drop-menu_opened {
    opacity: 1;
    pointer-events: unset;
  }
}